// Offer Carousel Slideshow
let arrayCarousel;
arrayCarousel = ['./static/images/offers/offer1.jpg',
    '../public/images/offers/offer2.jpg',
    '../public/images/offers/offer3.jpg',
    '../public/images/offers/offer4.jpg',
    '../public/images/offers/offer5.jpg'
];


for (let i = 0; i < arrayCarousel.length; i++) {
    const imageURL = arrayCarousel[i];
    console.log('imageURL', imageURL);

    const divElement = document.createElement("div");
    const imgElement = document.createElement("img");
    imgElement.src = imageURL;
    divElement.classList.add("item");
    divElement.appendChild(imgElement);
    const carouselContainer = document.createElement("div");
    carouselContainer.appendChild(divElement);
}

//Plp-tablet layout
const section = document.createElement("SECTION");
section.className("main-section");
const sidebar = document.createElement("menu");
section.appendChild(sidebar);